* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

*::after,
*::before {
    box-sizing: border-box;
}

.body {
    font-family: 'helveticaneue', 'Arial', sans-serif;
    font-weight: 400;
    font-style: normal;
    margin: 0 auto;
    min-width: 350px; 
}
.fixed {
    overflow: hidden;
}

.img-responsive {
    max-width: 100%;
    height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
a {
    text-decoration: none;
}
.container {
    max-width: 1210px;
    min-width: 350px;
    margin: 0 auto;
    padding: 0 15px;
}
